<template>
  <div class="top-bar">
    <div class="logo-section">
      <router-link to="/">
        <img src="@/assets/ninelife_logo.png" alt="Ninelife Logo" class="logo ninelife-logo" />
      </router-link>
      <router-link to="/">
        <img src="@/assets/makekit_logo.png" alt="Makekit Logo" class="logo makekit-logo" />
      </router-link>
    </div>
    <nav class="menu">
      <DropdownMenu 
        title="Product" 
        :items="productItems" 
        :isActive="activeDropdown === 'Product'" 
        @toggle="toggleDropdown('Product')" 
      />
      <DropdownMenu 
        title="Docs" 
        :items="docsItems" 
        :isActive="activeDropdown === 'Docs'" 
        @toggle="toggleDropdown('Docs')" 
      />
      <!-- 將 Contact 改為與其他按鈕一致的樣式 -->
      <router-link class="menu-item button-style" to="/contact">Contact</router-link>
      <DropdownMenu 
        title="About" 
        :items="aboutItems" 
        :isActive="activeDropdown === 'About'" 
        @toggle="toggleDropdown('About')" 
      />
    </nav>
  </div>
</template>

<script>
import DropdownMenu from './DropdownMenu.vue';

export default {
  components: { DropdownMenu },
  data() {
    return {
      activeDropdown: null,
      productItems: [
        { text: 'Air:bit', link: '/airbit' },
        { text: 'Hover:bit', link: '/hoverbit' },
        { text: 'Wheel:bit', link: '/wheelbit' },
        { text: 'Bubble:bit', link: '/bubblebit' }
      ],
      docsItems: [
        { text: 'Air:文件', link: '/airbitdocs' },
        { text: 'Hover:文件', link: '/hoverbitdocs' },
        { text: 'Wheel:文件', link: '/wheelbitdocs' },
        { text: 'Bubble:文件', link: '/bubblebitdocs' }
      ],
      aboutItems: [
        { text: '關於我們', link: '/about' },
        { text: 'Blog', link: '/blog' }
      ]
    };
  },
  methods: {
    toggleDropdown(menu) {
      this.activeDropdown = this.activeDropdown === menu ? null : menu;
    }
  }
};
</script>

<style scoped>
.top-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #ffffff;
  padding: 10px 20px;
  border-bottom: 1px solid #e0e0e0;
}

.logo-section {
  display: flex;
  align-items: center;
}

.logo {
  height: auto;
  margin-right: 10px;
}

.ninelife-logo {
  width: 120px;
}

.makekit-logo {
  width: 120px;
}

.menu {
  display: flex;
  gap: 20px;
}

/* 按鈕樣式 */
.menu-item,
.button-style {
  text-decoration: none;
  color: #ffffff;
  font-weight: bold; /* 設置字體為粗體 */
  padding: 10px 20px;
  background-color: #003377; /* 藍色背景 */
  border-radius: 4px;
  transition: background-color 0.3s;
}

.menu-item:hover,
.button-style:hover {
  background-color: #003377; /* hover 狀態更深的藍色 */
}
</style>
